@import "../../assets/styles/variables";

.button {
    background-color: $main-color-lighter;
    color: #fff;
    width: 150px;
    height: 34px;

    &:hover {
        opacity: 0.85;
        cursor: pointer;
    }

    &:disabled {
        background-color: $gray-color;
        pointer-events: none;
    }
}